import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronDown } from 'lucide-react';
import { useDeviceType } from '../hooks/useDeviceType';

function FAQ() {
  const [expandedIndex, setExpandedIndex] = useState<number | null>(null);
  const deviceType = useDeviceType();
  const isMobile = deviceType === 'mobile';

  const faqs = [
    {
      question: "What is the minimum age requirement?",
      answer: "You must be 18 or older to enter Sapphire Lounge and enjoy our shisha services. Valid ID is required for entry."
    },
    {
      question: "Do you take reservations?",
      answer: "Yes, we highly recommend making reservations, especially for weekends and special events. You can book through our website or call us directly."
    },
    {
      question: "What types of shisha/hookah do you offer?",
      answer: "We offer a wide variety of non-tobacco & nicotine-free flavours, including traditional and modern blends. Our menu includes both standard and premium flavours with options to add extras like an Ice Base, Large Head or an Additional Flavour."
    },
    {
      question: "How long is a typical shisha session?",
      answer: "A standard session typically last between 2 - 3 hours. You can extend your stay to take advantage of the social setting & atmosphere. But if we have large group bookings, we may ask you to vacate your seat for them."
    },
    {
      question: "Do you serve food and drinks?",
      answer: "Yes, we offer a selection of hot and cold beverages, light snacks, and desserts to complement your shisha experience."
    },
    {
      question: "What payment methods do you accept?",
      answer: "We accept all major credit/debit cards and cash payments. Some special events may require advance payment."
    },
    {
      question: "Is there a dress code?",
      answer: "No, we want to be able to welcome everyone. So our dress code is open to casual & smart wear"
    },
    {
      question: "Can I bring my own shisha/tobacco?",
      answer: "No, for quality control and safety reasons, we only use our own premium shisha products and equipment."
    },
    {
      question: "Do you have outdoor seating?",
      answer: "No, we are an indoor-only venue offering comfortable booth and regular seating options in our stylishly designed space."
    },
    {
      question: "What health & safety measures do you have in place?",
      answer: "We follow all current local health guidelines, including regular sanitization of equipment, cleanliness of premises, and staff health monitoring."
    },
    {
      question: "What are your loyalty program tiers?",
      answer: "We offer three exclusive tiers: Silver, Gold, and Sapphire. Each tier provides increasing benefits including discounts, priority bookings, and special perks. Visit our Loyalty page for full details."
    },
    {
      question: "Do you accommodate group bookings?",
      answer: "Yes, we welcome group bookings! We have spacious booth seating perfect for groups. For larger parties, please contact us directly to discuss arrangements and any special requirements."
    },
    {
      question: "Is there parking available?",
      answer: "While we don't have dedicated parking, there are several public parking options nearby. We recommend using the local paid parking facilities within walking distance."
    },
    {
      question: "Is Wi-Fi available?",
      answer: "Yes, we provide complimentary high-speed Wi-Fi to all our guests. Just ask our staff for the current password."
    }
  ];

  const handleQuestionClick = (index: number) => {
    if (isMobile) {
      setExpandedIndex(expandedIndex === index ? null : index);
    }
  };

  return (
    <div className="min-h-screen pt-24 pb-12 bg-[#020B18]">
      <div className="container mx-auto px-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-center mb-8"
        >
          <h1 className="text-3xl md:text-4xl font-bold mb-3 bg-clip-text text-transparent bg-gradient-to-r from-primary-300 to-accent-400">
            Frequently Asked Questions
          </h1>
          <p className="text-gray-400 text-sm max-w-2xl mx-auto">
            Find answers to common questions about Sapphire Shisha Lounge.
          </p>
        </motion.div>
        <div className="max-w-3xl mx-auto">
          <div className={`space-y-${isMobile ? '3' : '6'}`}>
            {faqs.map((faq, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ 
                  duration: isMobile ? 0.2 : 0.5, 
                  delay: isMobile ? index * 0.05 : index * 0.1 
                }}
                className={`bg-dark-900/50 backdrop-blur-sm rounded-lg border border-accent-700/20 ${
                  isMobile ? 'p-4' : 'p-6'
                }`}
                onClick={() => handleQuestionClick(index)}
              >
                <div className="flex justify-between items-start">
                  <h3 className={`font-semibold text-white ${
                    isMobile ? 'text-base pr-8' : 'text-lg'
                  }`}>
                    {faq.question}
                  </h3>
                  {isMobile && (
                    <ChevronDown 
                      className={`w-5 h-5 text-gray-400 transform transition-transform ${
                        expandedIndex === index ? 'rotate-180' : ''
                      }`}
                    />
                  )}
                </div>
                
                {/* Desktop shows answer always, Mobile shows only when expanded */}
                <AnimatePresence>
                  {(!isMobile || expandedIndex === index) && (
                    <motion.p
                      initial={isMobile ? { height: 0, opacity: 0 } : false}
                      animate={isMobile ? { height: 'auto', opacity: 1 } : false}
                      exit={isMobile ? { height: 0, opacity: 0 } : false}
                      transition={{ duration: 0.2 }}
                      className={`text-gray-300 ${
                        isMobile ? 'text-sm mt-2' : 'mt-2'
                      }`}
                    >
                      {faq.answer}
                    </motion.p>
                  )}
                </AnimatePresence>
              </motion.div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default FAQ;
